.app {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 500px;
  height: 400px;
  border-radius: 18px;
  background-color: #54bde1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  width: 496px;
  height: 320px;
  background-color: white;
}

.title {
  font-size: 48px;
  text-align: center;
}

.timer {
  font-size: 40px;
  text-align: center;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons > button {
  margin: 10px;
  padding: 10px;
}
